
import {isMobile} from 'react-device-detect';
import PlayButton from '../buttons/PlayButton.js';
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './shuffle.scss';

const WIDTH = 240;

const ShuffleButton = ({buttonTextColor, playerRef}) => { 

    let [progress, setProgress] = useState(0);
    let [playing, setPlaying] = useState(0);
    let [started, setStarted] = useState(false);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return x => window.removeEventListener("message", onMessage, false);
    }, [setProgress, setPlaying, setStarted]);

    const onMessage = useCallback(e => {
        if (e.data.type === "song-player-progress") {
            setProgress(parseInt(e.data.body));
        }
        if (e.data.type === "jacker-playback-status") {
            setPlaying(parseInt(e.data.body));
            setStarted(true);
        }
    }, [setProgress, setPlaying, setStarted]);

    const onPlayClicked = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "jacker-pause-toggle",
            body: true,
        }, "*");
    }, []);

    let className = "shuffle-buttons";

    if (!started) {
        return '';
    }

    let style = {
        borderColor: buttonTextColor
    };
    return (
        <div className={className}>
          <PlayButton color={buttonTextColor} style={style} isPlaying={playing} play={onPlayClicked}/>
          <div style={style} className="slider-container">
            <div
              style={{
                  backgroundColor: buttonTextColor,
                  left: WIDTH*progress/100}} className="slider-inner"/>
          </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ShuffleButton);
