import React, {useRef, useCallback} from 'react';
import {ethers} from 'ethers';
import {infuraProvider} from 'wagmi/providers/infura';
import { IERC721Metadata__factory} from "@zoralabs/v3/dist/typechain/factories/IERC721Metadata__factory";
import { useSigner } from 'wagmi';
const infuraId = "2f3dce2af1044e88b4851c3d73c3d00c";

export const useTokenMetadata = () => {

    const getTokenMetadata = useCallback(async (contract, tokenId) => {
        let provider = new ethers.providers.InfuraProvider("mainnet", infuraId);
        const tokens  = IERC721Metadata__factory.connect(contract, provider);
        try {
            tokenId = ethers.BigNumber.from(tokenId);
            let meta = await tokens.tokenURI(tokenId);
            return meta;
        } catch (e) {
            return undefined;
        }
    }, []);

    return getTokenMetadata;
 
};
