import {useInterval} from './useInterval.js';
import {useTokensOnSale} from './useTokensOnSale.js';
import { useAccount } from 'wagmi';
import React, {useRef, useEffect, useCallback, useState} from 'react';

export const useOwnsAnySpore = () => {
    const {data: account, isError, isLoading} = useAccount();
    const {tokensOnSale} = useTokensOnSale();
    let [locked, setLocked] = useState(false);
    let [isSporeOwner, setIsSporeOwner] = useState();

    useEffect(() => {
        if (tokensOnSale && account && account.address) {
            let _tokensOnSale = window.OWN_FAKE_SPORE_FLAG ? tokensOnSale.map(x =>
                ({
                    ... x,
                    owner: account.address})) : tokensOnSale;
            let address = account.address.toLowerCase();
            if (_tokensOnSale.some(x =>
                x &&
                    x.owner &&
                    x.owner.toLowerCase() === address)) {
                setIsSporeOwner(true);
                setLocked(false);
            }
        }
    }, [tokensOnSale, account, setIsSporeOwner, setLocked]);

    return isSporeOwner;
};
 
